 "use client"
 import { useState } from "react"

function Createform(){
    const[data,setdata]=useState({
        name:"",
        email:"",
        password:""
    })
    function handlesubmit(){
        const res=axio
    }
    return(
        <div className="h-screen bg-blue-950 p-5">
            <div className="place-self-center justify-items-center bg-gray-300 w-1/2 h-auto space-y-2 p-4">
            <form onSubmit={handlesubmit}>
             <h1>FORM</h1>
             <input
             placeholder="Name"
             className="p-2 block border-1"
             value={data.name}
             onChange={(e)=>setdata(e.target.value)}
             />
              <input
             placeholder="Email"
             className="p-2 block border-1"
             value={data.email}
             onChange={(e)=>setdata(e.target.value)}
             />
              <input
             placeholder="Password"
             className="p-2 block border-1"
             value={data.password}
             onChange={(e)=>setdata(e.target.value)}
             />
             <div className="p-2"><button className="hover:bg-blue-400 rounded p-2">Submit</button></div>
             </form>
            </div>
        </div>
    )
}
export default Createform